      position: relative;
      margin-bottom: 2em;
      transition: all 0.5s ease;
      cursor: pointer;
      &.selected,
      &:hover {
        &::before {
          background: black;
        }
      }
      &.selected h3 {
        font-weight: 700 !important;
      }

      &::before {
        content: "";
        height: 8px;
